<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <template #btnGroup>
      <!-- 顶部按钮组 -->
      <g-btn-group
        :buttonLists="btnList"
        @btnCallBack="btnGroupClick"
      ></g-btn-group>
    </template>

    <template #table>
      <g-rdo-table
        :expendResetTag="expendResetTag"
        :rdoId="rdoId"
        :handName="'测试表格'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-rdo-table>
    </template>

    <template #content>
      <create-or-edit-compRdo
        :rdoEditParams="rdoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick"
        v-if="isShowEdit"
      ></create-or-edit-compRdo>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import { GRdoPageBase } from "@/utils";
import CreateOrEditCompRdo from "./create-or-edit-compRdo/create-or-edit-compRdo.vue";
import GCopyRdo from "@/components/g-copy-rdo/g-copy-rdo.vue";

export default {
  name: "CompRdo",
  mixins: [GRdoPageBase],
  components: {
    CreateOrEditCompRdo,
  },
  data() {
    return {};
  },

  mounted() {},

  methods: {
    getBtnList() {
      // 获取按钮列表
      this.btnList = [
        {
          text: this.AppConst.button.create,
          icon: this.AppConst.icon.create,
          eventName: this.AppConst.button.create,
          display: true,
        },
        {
          text: this.AppConst.button.createVersion,
          icon: this.AppConst.icon.createVersion,
          eventName: this.AppConst.button.createVersion,
          display: this.rdoBaseId,
        },
        {
          text: this.AppConst.button.copy,
          icon: this.AppConst.icon.copy,
          eventName: this.AppConst.button.copy,
          display: !!this.rdoId,
        },
        {
          text: this.AppConst.button.copyVersion,
          icon: this.AppConst.icon.copyVersion,
          eventName: this.AppConst.button.copyVersion,
          display: !!this.rdoId,
        },

        {
          text: this.AppConst.button.delete,
          icon: this.AppConst.icon.delete,
          eventName: this.AppConst.button.delete,
          display: this.rdoBaseId && this.isRevOfRcdId,
        },
        {
          text: this.AppConst.button.deleteVersion,
          icon: this.AppConst.icon.deleteVersion,
          eventName: this.AppConst.button.deleteVersion,
          display: !!this.rdoId && !this.isRevOfRcdId,
        },
        {
          text: this.AppConst.button.refresh,
          icon: this.AppConst.icon.refresh,
          eventName: this.AppConst.button.refresh,
          right: true,
          display: this.isShowEdit,
        },
        {
          text: this.AppConst.button.save,
          icon: this.AppConst.icon.save,
          eventName: this.AppConst.button.save,
          display: this.isShowEdit,
          right: true,
          type: "primary",
        },
      ];
    },
    fetchData(request) {
      this.loading = true;
      setTimeout(() => {
        request.finishedCallback();
        // 填充dataList 20条数据
        let dataList = [];
        for (let i = 0; i < 25; i++) {
          // 随机id 格式 xxxx-xx
          let revOfRcdId = `${Math.floor(Math.random() * 10000)}-${Math.floor(
            Math.random() * 10000
          )}`;

          dataList[i] = {
            id: i + 1,
            name: "测试数据" + (i * this.pageNumber + 1),
            revOfRcdId: revOfRcdId,
            rdoList: [
              {
                revision: "默认版本" + (i * this.pageNumber + 1),
                id: revOfRcdId,
              },
              {
                revision: "测试版本" + (i * this.pageNumber + 1),
                id: i + 2,
              },
            ],
          };
        }

        // filter 过滤
        if (this.filterText) {
          dataList = dataList.filter((item) => {
            return item.name.indexOf(this.filterText) > -1;
          });
        }

        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
    copy() {
      this.createModal(
        { title: "复制", width: "600px" },
        {
          component: GCopyRdo,
          props: {
            entity: {
              copyValue: this.dataList[0].name,
              value: this.dataList[0].name,
            },
          },
          on: {
            success: (res) => {
              console.log(res);
              // 手动延时
              this.loading = true;
              setTimeout(() => {
                this.loading = false;
                this.copySuccess(res.value, res.copyValue);
                this.refreshGoFirstPage();
              }, 500);
            },
          },
        }
      );
    },
    copyVersion() {
      const data = {
          id: this.rdoId,
          labelName: 'ProductBomName',
          value: null,
          name: null
        };
      this.createModal(
        { title: "复制版本", width: "600px" },
        {
          component: GCopyRdo,
          props: {
            entity: {
              copyValue: this.dataList[0].name,
              value: this.dataList[0].name,
            },
          },
          on: {
            success: (res) => {
              console.log(res);
              // 手动延时
              this.loading = true;
              setTimeout(() => {
                this.loading = false;
                this.copySuccess(res.value, res.copyValue);
                this.refreshGoFirstPage();
              }, 500);
            },
          },
        }
      );
    },
    delete() {
      // 删除 延时
      this.$confirm({
        title: "确认删除",
        content: "确认删除吗?",
        onOk: () => {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            // 消息提示
            this.deleteSuccess();
            // 刷新
            this.refresh();
          }, 500);
        },
      });
    },
    /** 删除版本 */
    deleteRev() {
        // 删除 延时
      this.$confirm({
        title: "确认删除",
        content: "确认删除吗?",
        onOk: () => {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            // 消息提示
            this.deleteSuccess();
            // 刷新
            this.refresh();
          }, 500);
        },
      });
      },
  },
};
</script>

<style lang="scss" scoped></style>
